@import url("https://fonts.googleapis.com/css2?family=Yellowtail&display=swap");

.contact {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  list-style-type: none;
  -webkit-filter: invert(1);
          filter: invert(1); /*To invert colors of icons from black to white*/
  margin: 50px 0 50px 0;
}

.contact li a {
  width: 300px;
  height: auto;
}

#Contact h2 {
  font-family: "Yellowtail", cursive;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10%;
  padding: 5%;
  margin-top: 5%;
  font-size: 6em;
  -webkit-transform: rotate(-18deg);
      -ms-transform: rotate(-18deg);
          transform: rotate(-18deg);
}

#Contact {
  height: auto !important;
}

.text-pop-up-top {
	-webkit-animation: text-pop-up-top 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) 5s both;
	        animation: text-pop-up-top 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) 5s both;
}

